<template>
  <div>
    <Carousel
      class="more-products--carousel"
      :autoplay="0"
      :itemsToShow="2"
      :itemsToScroll="1"
      :breakpoints="useBreakPoint ? breakpoints : {}"
      snapAlign="start"
    >
      <Slide
        v-for="product of products"
        :key="product.sku"
        class="more-products--slide position-relative"
      >
        <ProductCard :product="product" />
      </Slide>

      <template #addons v-if="products.length > 1">
        <Navigation>
          <template #prev>
            <i class="lnr lnr-chevron-left"></i>
          </template>
          <template #next>
            <i class="lnr lnr-chevron-right"></i>
          </template>
        </Navigation>
      </template>
    </Carousel>
  </div>
</template>

<script setup>
import { Carousel, Slide, Navigation } from "vue3-carousel/dist/carousel.es.js";
import ProductCard from "@/base/core/components/ProductCard";

defineProps({
  products: { type: Array },
  useBreakPoint: { type: Boolean, default: false },
});

const breakpoints = {
  // 768px and up
  768: {
    itemsToShow: 3,
  },
  // 992px and up
  992: {
    itemsToShow: 4,
  },
};
</script>
<style scoped>
:deep(.carousel__prev) {
  transform: translateY(-50%) translateX(-100%);
  font-family: "system";
  padding: 8px;
  margin-right: -8px;
  font-size: 16px;
}

:deep(.carousel__next) {
  transform: translateY(-50%) translateX(100%);
  font-family: "system";
  padding: 8px;
  margin-left: -8px;
  font-size: 16px;
}
</style>
